const styles = {
  results: {
    spinner: {
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)'
    }
  },
  '@media (min-width: 641px)' : {},
  '@media (min-width: 1025px)': {}
};

export default styles;


